
import mixin from '../libs/mixin'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import FormPreview from 'vux/src/components/form-preview/index.vue'

import $ from 'jquery'
export default {
  components: {
    Group,
    Cell,
    FormPreview
  },
  mixins: [mixin],
  mounted: function(){
  	this.setDocumentTitle("home")
  },
  data () {
    return {
      msg1: 'home',
      type:'',
      price:'$6600.00',
      list: [{
        label: '',
        value: ''
      }, {
        label: '',
        value: ''
      }, {
        label: '',
        value: ''
      }],
      buttons1: [{
        style: 'default',
        text: ''
      }, {
        style: 'primary',
        text: '',
        link: '/calendar'
      }],
      buttons2: [{
        style: 'default',
        text: '',
        link: '/previewer'
      }, {
        style: 'primary',
        text: '',
        link: '/colorpicker'
      }]
    }
  }
}



